        h2 1.3 Perfil y estilo de consumidor
    p Para el anlisis de una marca o mercado, se requiere de la categorizacin de grupos de personas con caractersticas similares, estudio que permitir identificar su comportamiento, consumo e intereses, permitiendo as que el producto o servicio a ofrecer sea mucho ms especfico y certero con el cliente o nicho de mercado. 
    
