        h4.mt-5.ps-3 Quin es el consumidor? 
        p.mb-5.ps-3 Se entiende por consumidor al individuo que requiere satisfacer cierta necesidad a travs de la compra u obtencin de determinados productos. El consumidor no es un simple agente #[b pasivo ] que espera a que le ofrezcan productos, es un agente #[b activo ] con el poder suficiente para lograr cambios en las ofertas y hasta en las mismas empresas, para que se ajusten a sus requerimientos y necesidades.
  
