        h3(style="color: #673AB7") \\\ Perfil del consumidor
      p.mb-3 Es la descripcin de las caractersticas de los consumidores de un producto o usuarios de un servicio determinado y un modelo de pensamiento. Es una de las variables para la definicin de cualquier estrategia de #[em marketing]. El perfil no se determina slo por la edad ni por el segmento socio econmico.
      p.mb-4 A continuacin se describen los diferentes perfiles:

