    .row.mt-5
      .titulo-segundo.color-acento-contenido
        h3(style="color: #673AB7") \\\ Estilos  del consumidor
      h4 Qu es el estilo?
      p.mb-5 Coherencia entre el ser y el parecer. La relacin que tiene el modelo de pensamiento y la actitud personal define el estilo de cada individuo. El estilo refleja todos los aspectos de una persona, el pensamiento, las acciones y la relacin con los dems y los objetos, adems sus gustos, preferencias, motivaciones, valoraciones necesidades y actividades reflejadas en sus actitudes.

    .row.justify-content-center.align-items-center.mb-4(style='background-color: #FEF5E5')(data-aos="zoom-in-left")
