      .col-sm-12.col-md-7.col-lg-7
        p.mt-4 #[b El estilo:] Es una seleccin y acumulacin de vivencias, referentes, culturas e influencias que se prioriza, valora y manifiesta con nuestra actitud y expresin a travs de los objetos que elegimos para llevar en nuestra indumentaria y nuestro diario vivir, lo que se contribuye en la coherencia en el habitus, hbito, hbitat que determinar un estilo personal, particular y plural.
        ul.ul.lista-ul--color.vmorada.fa-ul
          li.mb-0 
            i.lista-ul__vineta.mb-0
            span.mb-0.text-bold El estilo personal es un proceso creativo constante y atractivo, que evoluciona el ser.
          li.mb-0
            i.lista-ul__vineta.mb-0
            span.mb-0.text-bold No tiene reglas, excepto las que uno se ponga; las cuales tambin pueden ser ajustadas.

