        .row.ps-3
          .col-sm-12.col-md-7.col-lg-7.mb-4.mb-md-0
            h4 Estilo Bohemio
            p El estilo boho o bohemio, es un estilo originalmente popularizado por los bohemios en la dcada de los 60, un grupo de gente que rechazaba los valores morales tradicionales vivan un estilo de vida nmada. No sola ser considerado de moda por los estndares del momento. Originalmente se usaba por comodidad y para desafiar las normas de vestir tradicionales.
            p.mb-2 #[b Caractersticas: ]
            ul.lista-ul--color.vmorada.fa-ul
              li.mb-0 
                i.lista-ul__vineta.mb-0
                span.mb-0 El estilo bohemio es relajado, confortable y lo ms importante, una expresin personal. 
              li.mb-0 
                i.lista-ul__vineta.mb-0
                | Valora la comodidad.
              li.mb-0
                i.lista-ul__vineta.mb-0
                span.mb-0 Las mujeres optan por vestidos sueltos, tnicas, faldas de volados y pantalones amplios o tipo gaucho.
            .cajon.borde-reondo(style='background-color: #FEF5E5') 
              p.p-4.mb-0 No se puede tener un estilo bohemio usando prendas ajustadas; esto no funciona. Tiene que tener la posibilidad de moverse, bailar y escalar.  
              p.text-center.mb-0.mt-0 #[b (Diane Von Furstenberg) ]
