        .row.ps-3.mt-3
          .col-sm-12.col-md-7.col-lg-7.mb-4.mb-md-0
            h4 Estilo Minimalista
            p El concepto naci en los aos sesenta, con la idea de menos es ms, del arquitecto Mies Van Der Rohe, quien propuso un estilo de vida que rescata la importancia de la persona, frente a una sociedad consumista. A grandes rasgos, trata no slo de usar poco, sino de encontrar el equilibrio entre reducir y exagerar en la decoracin, los detalles y las siluetas.
