            ul.lista-ul--color.vmorada.fa-ul
              li.mb-0 
                i.lista-ul__vineta.mb-0
                span.mb-0 El estilo minimalista requiere de disciplina, consistencia y gran atencin por la calidad.
              li.mb-0 
                i.lista-ul__vineta.mb-0
                span.mb-0 La mayora de los minimalistas son metdicos en el momento de vestirse y tienen un gran sentido y conciencia de s mismos.
              li.mb-0
                i.lista-ul__vineta.mb-0
                span.mb-0 Son muy fieles a algunas marcas que les garantizan cortes y diseos perfectos, telas de alta calidad y tecnolgicas.
