              p.p-4.mb-0.mt-0 En el minimalismo no se trata de abandonar los diseos o los estampados. Yo veo al minimalismo como la filosofa que envuelve el total sentido del balance, conociendo cundo quitar o sustraer algo. Es un vicio por los cortes absolutamente impecables, juegos tranquilos del color siluetas muy fuertes y limpias.  
